* {
  box-sizing: border-box;
}

body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

/* placeholders */
::placeholder {
  opacity: 1; /* Chrome, Firefox, Opera, Safari 10.1+ */ /* Firefox */
  color: var(--lw_grey-light);
}

::-moz-placeholder {
  opacity: 1; /* Chrome, Firefox, Opera, Safari 10.1+ */ /* Firefox */
  color: var(--lw_grey-light);
}

:-ms-input-placeholder {
  opacity: 1; /* Chrome, Firefox, Opera, Safari 10.1+ */ /* Firefox */
  color: var(--lw_grey-light);
}

:-ms-input-placeholder {
  color: var(--lw_grey-light);
}

::-ms-input-placeholder {
  color: var(--lw_grey-light); /* Microsoft Edge */
}

/* browser scrollbar */
:root {
  /* browser scroll behaviour */
  -ms-scroll-snap-type: y proximity;
  scroll-snap-type: y proximity;
  scroll-behavior: smooth;
}

body::-webkit-scrollbar {
  width: 0.5em;
}

body::-webkit-scrollbar-track {
  background: #000;
}

body::-webkit-scrollbar-thumb {
  border-radius: 0.25em;
  background-color: #666;
}

:focus-visible {
  outline: -webkit-focus-ring-color auto 1px;
}

select {
  text-transform: none;
}

input {
  overflow: visible;
}

input,
optgroup,
select,
textarea {
  line-height: inherit;
  font-family: inherit;
  font-size: inherit;
}

input:hover:not(:disabled):not(.disabled),
optgroup:hover:not(:disabled):not(.disabled),
select:hover:not(:disabled):not(.disabled),
textarea:hover:not(:disabled):not(.disabled) {
  cursor: pointer;
}

html,
body {
  min-height: 100vh;
}

.wp-site-blocks {
  display: flex;
  flex-direction: column;
}

footer {
  margin-block-start: auto !important;
}

.wp-block-spacer {
  margin-block-start: 0 !important;
}

/*
 * Alignment styles.
 *
 * Relevant issues:
 * https://github.com/WordPress/gutenberg/issues/35607
 * https://github.com/WordPress/gutenberg/issues/35884
 */
/*
//.wp-site-blocks,
// body > .is-root-container,
.edit-post-visual-editor__post-title-wrapper,
//.wp-block-group.alignfull,
.wp-block-group.has-background,
//.wp-block-cover.alignfull,
//.is-root-container .wp-block[data-align='full'] > .wp-block-group,
//.is-root-container .wp-block[data-align='full'] > .wp-block-cover {
	padding-left: var(--wp--custom--spacing--outer);
	padding-right: var(--wp--custom--spacing--outer);
}

//.wp-site-blocks .alignfull,
.wp-site-blocks > .wp-block-group.has-background,
.wp-site-blocks > .wp-block-cover,
.wp-site-blocks > .wp-block-template-part > .wp-block-group.has-background,
.wp-site-blocks > .wp-block-template-part > .wp-block-cover,
body > .is-root-container > .wp-block-cover,
body > .is-root-container > .wp-block-template-part > .wp-block-group.has-background,
body > .is-root-container > .wp-block-template-part > .wp-block-cover,
.is-root-container .wp-block[data-align='full'] {
	margin-left: calc(-1 * var(--wp--custom--spacing--outer)) !important;
	margin-right: calc(-1 * var(--wp--custom--spacing--outer)) !important;
	width: unset;
}
*/
.alignfull.has-background,
.wp-block-group.has-background {
  padding-top: 0;
  padding-bottom: 0;
  padding-left: var(--wp--custom--spacing--outer);
  padding-right: var(--wp--custom--spacing--outer);
}

.wp-site-blocks > * {
  margin-top: 0px;
  margin-bottom: 0px;
  padding-top: 64px;
  padding-bottom: 64px;
  padding-left: var(--wp--custom--spacing--outer);
  padding-right: var(--wp--custom--spacing--outer);
}
.wp-site-blocks > *:has(.alignfull) {
  padding-left: 0;
  padding-right: 0;
}
.wp-site-blocks > *:first-child, .wp-site-blocks > *:last-child {
  padding-top: 0;
  padding-bottom: 0;
}

:is(.alignfull),
.is-root-container .wp-block[data-align=full] > .wp-block-group,
.is-root-container .wp-block[data-align=full] > .wp-block-cover {
  margin-left: 0;
  margin-right: 0;
}

@media (min-width: 600px) {
  body {
    --wp--preset--font-size--small: 13px;
    --wp--preset--font-size--normal: 16px;
    --wp--preset--font-size--medium: 20px;
    --wp--preset--font-size--large: 28px;
    --wp--preset--font-size--x-large: 32px;
    --wp--preset--font-size--huge: 42px;
  }
}
@media (min-width: 992px) {
  body {
    --wp--preset--font-size--small: 14px;
    --wp--preset--font-size--normal: 18px;
    --wp--preset--font-size--medium: 24px;
    --wp--preset--font-size--large: 36px;
    --wp--preset--font-size--x-large: 42px;
    --wp--preset--font-size--huge: 56px;
  }
}
.has-huge-font-size {
  line-height: 1.25;
}

.has-x-large-font-size {
  line-height: 1.3;
}

.has-large-font-size {
  line-height: 1.4;
}

.has-medium-font-size {
  line-height: 1.5;
}

.has-normal-font-size {
  line-height: 1.7;
}

.has-small-font-size {
  line-height: 1.6;
}

.wp-block-search.wp-block-search__button-inside .wp-block-search__inside-wrapper, input:read-write,
textarea,
select, .comment-form textarea,
.comment-form input:not([type=submit]) {
  border-radius: 3px;
  color: var(--wp--preset--color--lw-fg);
  background: var(--wp--preset--color--lw-bg);
  border: 3px solid var(--wp--preset--color--lw-fg);
}

input[type=submit],
button[type=submit] {
  max-width: 100%;
  overflow: visible;
  border: 0;
  box-shadow: none;
  font-family: inherit;
  font-weight: 600;
  text-transform: uppercase;
  padding: 16px 32px;
  border-radius: 500px;
  font-size: var(--wp--preset--font-size--normal);
  transition: all 0.25s ease-in-out;
  color: var(--wp--preset--color--lw-fg-alt);
  background: var(--wp--preset--color--lw-accent);
  border-color: var(--wp--preset--color--lw-accent);
}

.comment-form {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 15px;
}
.comment-form > * {
  grid-column: 1/span 3;
}
.comment-form > .comment-form-author,
.comment-form > .comment-form-email,
.comment-form > .comment-form-url {
  grid-column: span 1;
}
input:read-write:not([type=search]),
textarea,
select {
  width: 100%;
  padding: 11px;
  font-size: var(--wp--preset--font-size--normal);
}

input {
  font-family: inherit;
}

textarea {
  resize: vertical;
  font-family: inherit;
}

.jsHideHeader {
  position: fixed !important;
  right: 0;
  left: 0;
  transition: all 0.25s;
  z-index: 10;
}

.wp-site-blocks > header {
  box-shadow: 0 0 rgba(0, 0, 0, 0);
}
.wp-site-blocks > header .wp-block-site-logo img {
  max-height: 85px;
  transition: max-height 0.25s;
}
.wp-site-blocks > header.has-shadow {
  box-shadow: var(--wp--preset--shadow--theme-soft);
}
.wp-site-blocks > header.is-squished .wp-block-site-logo img {
  max-height: 50px;
}

.wp-block-gallery.has-nested-images figure.wp-block-image figcaption, .wp-block-gallery.has-nested-images figcaption,
figure.wp-block-image figcaption {
  text-align: left;
  font-size: var(--wp--preset--font-size--small);
}

.wp-block-image.aligncenter {
  margin-left: auto;
  margin-right: auto;
}
.wp-block-image.alignleft {
  float: left;
  margin-right: 2em;
}
.wp-block-image.alignright {
  float: right;
  margin-left: 2em;
}

.wp-block-gallery.has-nested-images figcaption,
figure.wp-block-image figcaption {
  color: var(--wp--preset--color--lw-fg);
  opacity: 0.6;
}

.wp-block-gallery.has-nested-images figure.wp-block-image figcaption {
  background: rgba(0, 0, 0, 0.3);
  margin: 0;
  padding: 8px;
  opacity: 1;
}
.wp-block-gallery.has-nested-images figure.wp-block-image.is-style-rounded figcaption {
  opacity: 0.6;
  background: 0;
}

.wp-block-separator.is-style-wide {
  width: 100%;
}

.wp-block-categories.is-style-raft-pills {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  list-style: none;
  font-size: var(--wp--preset--font-size--small);
  padding-left: 0;
}
.wp-block-categories.is-style-raft-pills li {
  margin: 5px;
  background-color: var(--wp--preset--color--lw-bg-alt);
  border-radius: 25px;
  padding: 8px 24px;
}

.wp-block-post-terms {
  display: flex;
  flex-wrap: wrap;
}
.wp-block-post-terms.taxonomy-category a {
  padding: 8px 24px;
  margin: 5px;
  background-color: var(--wp--preset--color--lw-bg-alt);
  border-radius: 25px;
}
.wp-block-post-terms.taxonomy-category .wp-block-post-terms__separator {
  display: none;
}
.wp-block-post-terms.has-text-align-center {
  justify-content: center;
}
.wp-block-post-terms.has-text-align-right {
  justify-content: flex-end;
}

.wp-block-query-pagination-numbers .page-numbers:not(:last-child) {
  margin-right: 16px;
}

.wp-block-search .wp-block-search__button {
  background: var(--wp--preset--color--lw-accent);
  color: var(--wp--preset--color--lw-fg-alt);
  border-color: var(--wp--preset--color--lw-accent);
  border-radius: 5px;
}

.wp-block-navigation .wp-block-navigation__responsive-container-close svg,
.wp-block-navigation .wp-block-navigation__responsive-container-open svg {
  pointer-events: auto !important;
}

.wp-block-navigation:not(.has-background) .wp-block-navigation__responsive-container.is-menu-open {
  background: var(--wp--preset--color--lw-bg);
  color: var(--wp--preset--color--lw-fg);
}

.wp-block-navigation__responsive-container.is-menu-open {
  --navigation-layout-justification-setting: center;
  padding-top: 32px;
  padding-bottom: 64px;
  padding-left: var(--wp--custom--spacing--outer);
  padding-right: var(--wp--custom--spacing--outer);
}
.wp-block-navigation__responsive-container.is-menu-open > .wp-block-navigation__container {
  gap: 0;
}
.wp-block-navigation__responsive-container.is-menu-open .wp-block-navigation__container {
  gap: 24px !important;
}
.wp-block-navigation__responsive-container.is-menu-open .wp-block-navigation__container > li > a {
  font-size: var(--wp--preset--font-size--large) !important;
}
.wp-block-navigation__responsive-container.is-menu-open .wp-block-navigation__submenu-container {
  gap: 24px !important;
}

.wp-block-latest-posts img {
  display: block;
}
.wp-block-latest-posts .wp-block-latest-posts__post-title {
  display: block;
  font-size: var(--wp--preset--font-size--medium);
  font-weight: 600;
  margin-bottom: 10px;
}
.wp-block-latest-posts .wp-block-latest-posts__post-author,
.wp-block-latest-posts time {
  font-size: var(--wp--preset--font-size--small);
}

.wp-block-table.is-style-stripes tbody tr:nth-child(odd) {
  background-color: var(--wp--preset--color--lw-bg-alt);
}
.wp-block-table thead {
  border-bottom: 2px solid;
}
.wp-block-table tfoot {
  border-top: 2px solid;
}
.wp-block-table figcaption {
  font-size: var(--wp--preset--font-size--small);
  color: var(--wp--preset--color--lw-fg);
  opacity: 0.6;
}

.wp-block-buttons .wp-element-button,
.wp-block-buttons .wp-block-button__link {
  transition: all 0.35s ease-in-out;
}

